.Backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100; }

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 23px;
  width: 36px;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  box-sizing: border-box; }
  .toggle-button:focus {
    outline: none; }
  .toggle-button .toggle-button__line {
    width: 30px;
    height: 2px;
    background: #fff; }

.toolbar {
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  background: teal;
  height: 66px;
  z-index: 500; }
  .toolbar .toolbar__navigation {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
    background: teal; }
    .toolbar .toolbar__navigation .space {
      flex: 1 1; }
    .toolbar .toolbar__navigation .toolbar__navigation-items a {
      color: #fff;
      text-decoration: none;
      font-size: 1.2rem; }
      .toolbar .toolbar__navigation .toolbar__navigation-items a:hover {
        color: #7dd9e4; }
      .toolbar .toolbar__navigation .toolbar__navigation-items a:active {
        color: #7dd9e4; }
    .toolbar .toolbar__navigation .toolbar__navigation-items ul {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex; }
      .toolbar .toolbar__navigation .toolbar__navigation-items ul li {
        padding: 0 0.5rem; }
    .toolbar .toolbar__navigation .toolbar__logo {
      margin-left: 1rem; }
      .toolbar .toolbar__navigation .toolbar__logo a {
        color: #fff;
        text-decoration: none;
        font-size: 1.5rem; }
  @media (max-width: 768px) {
    .toolbar .toolbar__navigation-items {
      display: none; } }
  @media (min-width: 769px) {
    .toolbar .toolbar__toggle-button {
      display: none; }
    .toolbar .toolbar__logo {
      margin-left: 20px; } }

.side-drawer {
  height: 100vh;
  background: #fff;
  box-shadow: 2px 0 3px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.4s ease-out;
  transition: -webkit-transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out; }
  .side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  .side-drawer ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%; }
    .side-drawer ul li {
      margin: 0.5rem 0rem; }
      .side-drawer ul li a {
        color: #ff7979;
        text-decoration: none;
        font-size: 1.2rem; }
        .side-drawer ul li a:hover {
          color: #7ed6df; }
        .side-drawer ul li a:active {
          color: #7ed6df; }

@media (min-width: 769px) {
  .side-drawer {
    display: none; } }

.lds-roller {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 64px;
    height: 64px;
  }
  .lds-roller div {
    -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    -webkit-transform-origin: 32px 32px;
            transform-origin: 32px 32px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #fff;
    margin: -3px 0 0 -3px;
  }
  .lds-roller div:nth-child(1) {
    -webkit-animation-delay: -0.036s;
            animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
    top: 50px;
    left: 50px;
  }
  .lds-roller div:nth-child(2) {
    -webkit-animation-delay: -0.072s;
            animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
    top: 54px;
    left: 45px;
  }
  .lds-roller div:nth-child(3) {
    -webkit-animation-delay: -0.108s;
            animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
    top: 57px;
    left: 39px;
  }
  .lds-roller div:nth-child(4) {
    -webkit-animation-delay: -0.144s;
            animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
    top: 58px;
    left: 32px;
  }
  .lds-roller div:nth-child(5) {
    -webkit-animation-delay: -0.18s;
            animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
    top: 57px;
    left: 25px;
  }
  .lds-roller div:nth-child(6) {
    -webkit-animation-delay: -0.216s;
            animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
    top: 54px;
    left: 19px;
  }
  .lds-roller div:nth-child(7) {
    -webkit-animation-delay: -0.252s;
            animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
    top: 50px;
    left: 14px;
  }
  .lds-roller div:nth-child(8) {
    -webkit-animation-delay: -0.288s;
            animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
    top: 45px;
    left: 10px;
  }
  @-webkit-keyframes lds-roller {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes lds-roller {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
